body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
}

.MuiSvgIcon-root-1 {
  font-size: 4rem;
}
.container {
  max-width: 800px;
  display: flex;
  flex-direction: row;
  justify-content: center;
  margin: 0 auto;
}
header svg { 
  color: #ffffff;
  -webkit-transform: translateY(10%);
          transform: translateY(10%);
}

.header-star {
  font-size: 3rem;
}

h1 {
  display: flex;
  padding-left: 20px;
}

@media only screen and (max-width: 500px) {
  .container {
    flex-direction: column;
  }
  
  svg {
    margin: 0 auto;
  }

  h1 {
    margin: 0 auto;
    margin-top: 40px;
  }
}


textarea {
  width: 60%;
	height: 80px;
	border: 2px solid #cccccc;
	padding: 5px;
  outline: none;
  resize: none;
  font-size: 1.2rem;
}

.tableContainer {
  overflow-x:auto;
}

table { 
  margin: 0 auto;
  overflow: scroll;
}

th {
  background-color: #08a99e;
  color: white;
}

th,
tr {
  padding: 10px;
  text-align: center;
}

th,
td {
  border-bottom: 1px solid #ddd;
}

tr:hover {
  background-color: #f5f5f5;
}

.App {
  text-align: center;
}

.App-header {
  background-color: #5adbcf;
  min-height: 100px;
  padding: 18px;
  color: white;
}

.App-title {
  font-size: 2rem;
}

.App-intro {
  font-size: large;
}

svg { 
  color: #08a99e;
}

h2 {
  color: #08a99e;
  padding: 5px;
  padding-top: 0px;
}

button {
  display: block;
  margin: 0 auto;
  margin-top: 14px;
  padding: 8px 28px;
  color: #08a99e;
  border: 1px solid #08a99e;
  cursor: pointer;
  font-size: 1.5rem;
  border-radius: 4px;
  transition: all .2s linear;
}

button:hover {
  background-color: #08a99e;
  color: #ffffff;
}

button:disabled,
button[disabled] {
  opacity: 0.65; 
  color: #888888;
  cursor: not-allowed;
  border: 1px solid #888888;
}

button:disabled:hover {
  background-color: #ffffff;
  color: #888888;
  border: 1px solid #888888;
}

.reviewError {
  color: #ea2762;
}

.reviewScore {
  color: #08a99e;
}

.requiredFieldError {
  color: #ea2762;
}
